@import url(https://fonts.googleapis.com/css2?family=Poppins&display=swap);
* {
	margin: 0;
	padding: 0;
	box-sizing: border-box;
	font-family: "Poppins", sans-serif;
}

body {
	background-color: #222;
}

.map-container {
	height: 70vh;
	width: 100vw;
	display: flex;
	flex-direction: column;
	margin-top: 12px;
}

.header {
	background-color: crimson;
	color: whitesmoke;
	display: flex;
	flex-direction: column;
	justify-content: center;
	align-items: center;
	height: 7vh;
}

.jvectormap-tip {
	background-color: blueviolet;
	color: white;
	display: flex;
	flex-direction: column;
	align-items: center;
	padding: 0 20px;
	justify-content: center;
}

.jvectormap-tip .confirmed {
	color: skyblue;
}

.jvectormap-tip .deaths {
	color: crimson;
}

.jvectormap-tip .critical {
	color: orange;
}

.jvectormap-tip .recoveries {
	color: lime;
}

.jvectormap-tip .active {
	color: yellow;
}

.jvectormap-tip .flag {
	width: 60px;
}

article {
    background-color: blueviolet;
    color: #222;
    border-radius: 10px;
    margin-top: 5vh;
    width: 70vw;
    margin: auto;
}

h2 {
    display: flex;
    align-items: center;
    justify-content: center;
    padding-top: 10px;
}

.info_row {
    display: flex;
    justify-content: center;
    align-items: center;
    padding: 10px;
}

.info_row h3 {
    margin-left: 3%;
    margin-right: 3%;
}

.confirmed {
    color: skyblue;
}

.deaths {
    color: crimson;
}

.recovered {
    color: lime;
}
